.fixedcard {
  position: sticky;
  top: 7rem;
  min-width: auto;
  direction: rtl;
  border-radius: 20px;
  box-shadow: 2px 1px 10px 2px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.fixedcard .learnDrawing {
  font-weight: bold;
}
.fixedcard .egyPound {
  color: black;
  font-weight: bold;
  font-size: 15px;
}
.fixedcard .oldPrice {
  color: #777777;
  font-weight: bold;
  font-size: 13px;
  text-decoration: line-through #C1121F;
}
.fixedcard .egyPound2 {
  color: #777777;
  font-weight: bold;
  font-size: 11px;
}
.fixedcard .addToCartBtn {
  font-size: 12px;
  background-color: #C1121F;
  color: white;
  border-radius: 8px;
  min-width: auto;
}
.fixedcard .addToCartBtn:hover {
  background-color: #C1121F;
}
.fixedcard .heartIconBtn {
  border: 0;
  outline: 0;
  font-weight: 100;
  margin-right: 7px;
  background-color: #F3F3F3;
  color: black;
  width: fit-content;
  box-shadow: 0;
  border-radius: 10px;
  min-width: auto;
}
.fixedcard .heartIconBtn:hover {
  border: 0;
  outline: 0;
  background-color: #F3F3F3;
}
.fixedcard .shareIconBtn {
  border: 0;
  outline: 0;
  font-weight: 200;
  margin-right: 7px;
  background-color: #F3F3F3;
  color: black;
  width: fit-content;
  box-shadow: 0;
  border-radius: 10px;
  min-width: auto;
}
.fixedcard .shareIconBtn:hover {
  border: 0;
  outline: 0;
  background-color: #F3F3F3;
}
.fixedcard .cartIcon {
  font-size: 18px;
  margin-left: 1rem;
}
.fixedcard .greenAlert {
  height: fit-content;
  background-color: #E8F3EE;
  font-size: 11px;
  font-weight: bold;
  color: #198754;
  display: flex;
  align-items: center;
  margin-top: 1rem;
}
.fixedcard .greenAlert .alertSmallText {
  font-size: 11px;
}
.fixedcard .verificationIcon {
  font-size: inherit;
}
.fixedcard .rulesContainers {
  display: flex;
  align-items: center;
  margin-top: 1rem;
}
.fixedcard .rulesContainers .rulesIcons {
  font-size: 15px;
  margin-left: 1rem;
  color: #C1121F;
}
.fixedcard .redAlert {
  height: fit-content;
  background-color: #F9E8E9;
  font-size: 11px;
  color: black;
  display: flex;
  align-items: center;
  margin-top: 1rem;
}
.fixedcard .millionLearners {
  font-weight: bold;
  font-size: 11px;
  margin-left: 3px;
  margin-right: 3px;
}
.fixedcard .enterCoponContainer {
  margin-top: 1rem;
  margin-right: 1rem;
  font-size: 11px;
  color: "black";
  text-align: "center";
}
.fixedcard .enterCoponContainer .enterCopon {
  font-weight: bolder;
  font-size: 11px;
  margin-left: 3px;
  margin-right: 3px;
  color: #C1121F;
}

